___CSS_LOADER_EXPORT___.push([module.id, `.listTitle{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding: 1rem;
}

.listTitle > h2{
    flex: 1;
    max-width: 500px;
}

.addTodoBtn{
    border: dashed 2px white;
    padding: .5rem;
    border-radius: 50px;
    width: 15px;
    height: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: transform .2s ease, background-color .2s ease;
}

.addTodoBtn:hover{
    transform: scale(1.05);
    background-color: var(--bgColorTwo);
}

.addTodoBtn:active{
    transform: scale(1.1);
    background-color: var(--bgColorThree);
}

.addTodoBtn > button {
    cursor: pointer;
    font-size: 1.3rem;
    padding: 0px;
    background-color: transparent;
}

/* This takes care of the design of each todo on the lists */

.taskContainer {
    display: flex;
    flex-direction: column;
    gap: .5rem;
    padding: .5rem;
}

.taskContainer > div:hover{
    border: solid 2px var(--bgColorThree);
    background-color: var(--bgColorThree);
    transition: border .2s ease, background-color .2s ease;
}

.task {
    cursor: pointer;
    display: flex;
    align-items: center;
    padding: 1rem;
    border: solid 2px var(--bgColorThree);
    border-radius: 15px;
    min-width: 90%;
    margin: 0 auto;
}

.task > p{
    flex: 1;
    font-size: 1.1rem;
}

/* Task title */
.task > p:first-of-type {
    flex: 3;
    padding-left: 1rem;
    font-weight: bold;
}

.task > input {
    appearance: none;
    position: relative;
    margin: 0;
    width: .5em;
    height: .5em;
    border: 0.15em solid var(--textColor);
    border-radius: 50%;
    cursor: pointer;
    transition: background-color 0.2s, border-color 0.2s;
}

.task > input:checked::after {
    content: "✔"; 
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: .7rem;
}

.taskDelBtn{
    display: flex;
    align-items: center;
    justify-content: center;
}

.checked {
    text-decoration: line-through;
    color: white;
    background-color: rgba(255, 255, 255, 0.062);
}

.taskInfoContainer {
    position: absolute;
    max-width: 35%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 20;
    padding: 2rem;

    background-color: var(--bgColorTwo);
    border-radius: 10px; 
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3); 
}

.topPart{
    display: flex;
    font-size: .9rem;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
    margin-bottom: 1rem;
}

.topPart > .taskInfoTitle{
    flex: 1;
    text-align: center;
}

.closeBtnInput{
    font-weight: bold;
    padding: 0;
    font-size: .9rem;
    width: 2rem;
    height: 2rem;

    cursor: pointer;
    font-weight: bold;
    border-radius: 6px;
    background-color: var(--bgColorOne);
}

.middlePart{
    padding: 1rem;
}

.taskDescriptionInfo,
.taskDueDate,
.taskPriority{
    margin-bottom: 1rem; 

}

.taskDescriptionInfo > p:first-of-type,
.taskDueDate > p:first-of-type,
.taskPriority > p:first-of-type{
    opacity: .8;
    text-transform: uppercase;
    font-weight: bold;
    font-size: .6rem;
    padding-bottom: .2rem;
}

.taskDescription{
    opacity: .8;
    text-transform: uppercase;
    font-weight: bold;
    font-size: .75rem; 
    padding: .2rem .2rem .2rem 0;
}
